/* eslint-disable no-unused-vars */
import * as React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { Button, CardContent, Grid, Typography, Card, CardMedia, useMediaQuery } from '@mui/material';
import { createTheme } from '@material-ui/core';

import MainCard from 'ui-component/cards/MainCard';
import { gridSpacing } from 'store/constant';
import { Edit } from 'tabler-icons-react';

const CampaignPerformances = React.forwardRef((props, ref) => (
    <RouterLink ref={ref} to="/campaigns/performance" {...props} role={undefined} />
));

const CampaignEdit = React.forwardRef((props, ref) => <RouterLink ref={ref} to="/launch/index" {...props} role={undefined} />);

const CampaignSummary = () => {
    const theme = createTheme({
        breakpoints: {
            values: {
                xs: 0,
                sm: 600,
                md: 900,
                lg: 1400,
                xl: 1600
            }
        }
    });
    const matchesMD = useMediaQuery(theme.breakpoints.down('md'));
    return (
        <MainCard>
            <form>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12} sx={{ marginBottom: `${matchesMD ? '20px' : '45px'}` }}>
                        <Grid container alignContent="center" justifyContent="space-between">
                            <Grid item>
                                <Typography
                                    sx={{
                                        fontFamily: 'Inter',
                                        fontStyle: 'normal',
                                        fontWeight: '700',
                                        fontSize: '30px',
                                        lineHeight: '36px',
                                        color: '#FFFFFF'
                                    }}
                                >
                                    Campaign Summary
                                </Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} sx={{ marginBottom: `${matchesMD ? '20px' : '40px'}` }}>
                        <Grid container spacing={gridSpacing}>
                            <Grid
                                item
                                lg={5}
                                md={6}
                                sm={6}
                                xs={12}
                                paddingRight="40px !important"
                                sx={{ marginBottom: `${matchesMD ? '20px' : '0px'}` }}
                            >
                                <CardContent
                                    sx={{
                                        backgroundColor: '#36006844',
                                        boxShadow: '40px 7px 132px rgba(0,0,0,0.4)',
                                        borderRadius: '20px'
                                    }}
                                >
                                    <Typography
                                        sx={{
                                            fontFamily: 'Inter',
                                            fontStyle: 'normal',
                                            fontWeight: `${matchesMD ? '200' : '400'}`,
                                            fontSize: `${matchesMD ? '11px' : '22px'}`,
                                            lineHeight: `${matchesMD ? '15px' : '27px'}`,
                                            color: '#B9B9B9'
                                        }}
                                    >
                                        Campaign Name
                                    </Typography>
                                    <Typography
                                        sx={{
                                            fontFamily: 'Inter',
                                            fontStyle: 'normal',
                                            fontWeight: `${matchesMD ? '400' : '700'}`,
                                            fontSize: `${matchesMD ? '16px' : '29px'}`,
                                            lineHeight: `${matchesMD ? '18px' : '35px'}`,
                                            color: '#FF0676'
                                        }}
                                    >
                                        Steve Jobs Gaming
                                    </Typography>
                                </CardContent>
                            </Grid>
                            <Grid item lg={4} md={3} sm={3} xs={12} sx={{ marginBottom: `${matchesMD ? '20px' : '45px'}` }}>
                                <Grid item>
                                    <CardContent
                                        sx={{
                                            backgroundColor: '#36006844',
                                            boxShadow: '40px 7px 132px rgba(0,0,0,0.4)',
                                            borderRadius: '20px'
                                        }}
                                    >
                                        <Typography
                                            sx={{
                                                fontFamily: 'Inter',
                                                fontStyle: 'normal',
                                                fontWeight: `${matchesMD ? '200' : '400'}`,
                                                fontSize: `${matchesMD ? '11px' : '22px'}`,
                                                lineHeight: `${matchesMD ? '15px' : '27px'}`,
                                                color: '#B9B9B9'
                                            }}
                                        >
                                            Launch Date
                                        </Typography>
                                        <Typography
                                            sx={{
                                                fontFamily: 'Inter',
                                                fontStyle: 'normal',
                                                fontWeight: `${matchesMD ? '400' : '700'}`,
                                                fontSize: `${matchesMD ? '16px' : '29px'}`,
                                                lineHeight: `${matchesMD ? '18px' : '35px'}`,
                                                color: '#FFFFFF'
                                            }}
                                        >
                                            09.06.2022
                                        </Typography>
                                    </CardContent>
                                </Grid>
                            </Grid>
                            <Grid item lg={3} md={3} sm={3} xs={12}>
                                <Grid item>
                                    <CardContent
                                        sx={{
                                            backgroundColor: '#36006844',
                                            boxShadow: '40px 7px 132px rgba(0,0,0,0.4)',
                                            borderRadius: '20px'
                                        }}
                                    >
                                        <Typography
                                            sx={{
                                                fontFamily: 'Inter',
                                                fontStyle: 'normal',
                                                fontWeight: `${matchesMD ? '200' : '400'}`,
                                                fontSize: `${matchesMD ? '11px' : '22px'}`,
                                                lineHeight: `${matchesMD ? '15px' : '27px'}`,
                                                color: '#B9B9B9'
                                            }}
                                        >
                                            Winning Prize
                                        </Typography>
                                        <Typography
                                            sx={{
                                                fontFamily: 'Inter',
                                                fontStyle: 'normal',
                                                fontWeight: `${matchesMD ? '400' : '700'}`,
                                                fontSize: `${matchesMD ? '16px' : '29px'}`,
                                                lineHeight: `${matchesMD ? '18px' : '35px'}`,
                                                color: '#04B4DD'
                                            }}
                                        >
                                            100$
                                        </Typography>
                                    </CardContent>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <Grid container spacing={gridSpacing}>
                            <Grid item lg={5} md={6} sm={6} xs={12} paddingRight="40px !important">
                                <Grid container spacing={gridSpacing}>
                                    <Grid item xs={12} sx={{ marginBottom: `${matchesMD ? '20px' : '55px'}` }}>
                                        <CardContent
                                            sx={{
                                                backgroundColor: '#36006844',
                                                boxShadow: '40px 7px 132px rgba(0,0,0,0.4)',
                                                borderRadius: '20px'
                                            }}
                                        >
                                            <Typography
                                                sx={{
                                                    fontFamily: 'Inter',
                                                    fontStyle: 'normal',
                                                    fontWeight: `${matchesMD ? '200' : '400'}`,
                                                    fontSize: `${matchesMD ? '11px' : '22px'}`,
                                                    lineHeight: `${matchesMD ? '15px' : '27px'}`,
                                                    color: '#B9B9B9'
                                                }}
                                            >
                                                TargetAudience
                                            </Typography>
                                            <Typography
                                                sx={{
                                                    fontFamily: 'Inter',
                                                    fontStyle: 'normal',
                                                    fontWeight: `${matchesMD ? '400' : '700'}`,
                                                    fontSize: `${matchesMD ? '16px' : '29px'}`,
                                                    lineHeight: `${matchesMD ? '18px' : '35px'}`,
                                                    color: '#FFC857'
                                                }}
                                            >
                                                Lorem Ipsum Dummy
                                            </Typography>
                                        </CardContent>
                                    </Grid>
                                    <Grid item xs={12} sx={{ marginBottom: `${matchesMD ? '20px' : '45px'}` }}>
                                        <CardContent
                                            sx={{
                                                backgroundColor: '#36006844',
                                                boxShadow: '40px 7px 132px rgba(0,0,0,0.4)',
                                                borderRadius: '20px'
                                            }}
                                        >
                                            <Typography
                                                sx={{
                                                    fontFamily: 'Inter',
                                                    fontStyle: 'normal',
                                                    fontWeight: `${matchesMD ? '200' : '400'}`,
                                                    fontSize: `${matchesMD ? '11px' : '22px'}`,
                                                    lineHeight: `${matchesMD ? '15px' : '27px'}`,
                                                    color: '#B9B9B9'
                                                }}
                                            >
                                                Game Name
                                            </Typography>
                                            <Typography
                                                sx={{
                                                    fontFamily: 'Inter',
                                                    fontStyle: 'normal',
                                                    fontWeight: `${matchesMD ? '400' : '700'}`,
                                                    fontSize: `${matchesMD ? '16px' : '29px'}`,
                                                    lineHeight: `${matchesMD ? '18px' : '35px'}`,
                                                    color: '#43CC83'
                                                }}
                                            >
                                                Long Winter Cars
                                            </Typography>
                                        </CardContent>
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid
                                item
                                lg={7}
                                md={6}
                                sm={6}
                                xs={12}
                                paddingRight="40px !important"
                                sx={{
                                    borderRadius: '20px'
                                }}
                            >
                                <CardMedia
                                    component="img"
                                    image={require(`../../../assets/images/game-img1.jpg`)}
                                    width="100%"
                                    alt="green iguana"
                                    sx={{
                                        borderRadius: '20px',
                                        height: `${matchesMD ? '210px' : '285px'}`
                                    }}
                                />
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item sx={{ marginTop: `${matchesMD ? '35px' : '70px'}` }}>
                    <Button
                        component={CampaignPerformances}
                        to="/campaigns/performance"
                        variant="contained"
                        sx={{
                            borderRadius: '9.8px',
                            backgroundColor: '#FF0676',
                            width: '260px',
                            height: '45px',
                            fontSize: '18px',
                            fontWeight: '700',
                            marginRight: '35px'
                        }}
                    >
                        Launch Your Campaign
                    </Button>
                    <Button
                        component={CampaignEdit}
                        to="/launch/index"
                        variant="outlined"
                        sx={{
                            borderRadius: '9.8px',
                            border: '1px solid #04B4DD',
                            width: '130px',
                            height: '45px',
                            fontSize: '18px',
                            fontWeight: '600',
                            color: 'white'
                        }}
                        startIcon={<Edit />}
                    >
                        Edit
                    </Button>
                </Grid>
            </form>
        </MainCard>
    );
};

export default CampaignSummary;
